import { createRouter, createWebHistory,createWebHashHistory } from 'vue-router'
import install from "../components/install.vue";
import Home from '../views/Home.vue'
import menu from "../components/menu.vue";
import aafxButton from "../components/aafxButton.vue";
import aafxModal from "../components/aafxModal.vue";
import aafxSelector from "../components/aafxSelector.vue";
import aafxPopover from "../components/aafxPopover.vue";
import aafxTabs from "../components/aafxTabs.vue";
import aafxSkeleton from "../components/aafxSkeleton.vue";
import aafxInput from "../components/aafxInput.vue";
import aafxSwitch from "../components/aafxSwitch.vue";
import aafxRadio from "../components/aafxRadio.vue";
import aafxCheckbox from "../components/aafxCheckbox.vue";
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect:'/install',
    children:[
      {
        path:'/install',
        name:'install',
        component:install
      },
      {
        path:'/menu',
        name:'menu',
        component:menu
      },
      {
        path:'/fxButton',
        name:'fxButton',
        component:aafxButton
      },
      {
        path:'/fxModal',
        name:'fxModal',
        component:aafxModal
      },
      {
        path:'/fxSelector',
        name:'fxSelector',
        component:aafxSelector
      },
      {
        path: '/fxPopover',
        name: 'fxPopover',
        component: aafxPopover
      },
      {
        path: '/fxTabs',
        name: 'fxTabs',
        component: aafxTabs
      },
      {
        path: '/fxSkeleton',
        name: 'fxSkeleton',
        component: aafxSkeleton
      },
      {
        path: '/fxInput',
        name: 'fxInput',
        component: aafxInput
      },
      {
        path: '/fxSwitch',
        name: 'fxSwitch',
        component: aafxSwitch
      },
      {
        path: '/fxRadio',
        name: 'fxRadios',
        component: aafxRadio
      },
      {
        path: '/fxCheckbox',
        name: 'fxCheckbox',
        component: aafxCheckbox
      },
    ]
  },

]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
